@import url(https://fonts.googleapis.com/css2?family=Krona+One&family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Krona+One&family=Oswald&display=swap);
body {
  background: #E9F5FA;
  padding: 0;
  margin: 0;
  max-width: 100vw;
  min-height: 100vh;
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;  
}


.date {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #4DB0D3;
  font-family: 'Krona One', sans-serif;
  text-align: center;
}

.day-week {
  font-size: 1.5rem;
  text-transform: uppercase;
}

.day-name {
  font-size: 3.5rem;;
}
.day {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 160px;
    min-height: 450px;
    background-color: var(--background);  
    border-radius: 50px;  
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1), 11px 4px 34px rgba(32, 77, 92, 0.25);
}

.day-wrapepr {
  display: flex;
  flex-direction: column;
}

.icon {
    align-self: center;
    padding-top: 25px;
}

.temperature {
    font-family: 'Oswald', sans-serif;
  color: var(--temperature);
  font-size: 5.5rem;
}

.content {
  color: var(--content);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-bottom: 25px;
  font-size: 1.2rem;
}

.precipitation, .low {
  position: relative;
  top: 10px;
}

.cloud {
  --background: #4DB0D3;
  --temperature: #E6DF95;
  --content: #D3EBF4;
}

.clear {
  --background: #E6DF95;
  --temperature: #4DB0D3;
  --content: #247490;
}

.thunderstorm {
  --background: #0E2E3A;
  --temperature: #E6DF95;
  --content: #D3EBF4;
}

.snow {
  --background: #BCE1EF;
  --temperature: #0E2E3A;
  --content: #247490;
}

.partly-cloudy {
  --background: #4DB0D3;
  --temperature: #E6DF95;
  --content: #D3EBF4;
}

.rain, .drizzle {
  --background: #2B8BAD;
  --temperature: #E6DF95;
  --content: #D3EBF4;
}
.wrapper {
    padding: 0 20px;
    display: flex;
    grid-gap: 20px;
    gap: 20px;
    flex-wrap: wrap;
}
.location {
    font-family: 'Krona One', sans-serif;
    text-align: center;
    font-style: bolder;
    font-size: 4.5rem;
    color: #23637a;
    -webkit-font-feature-settings: "smcp";
            font-feature-settings: "smcp";
    font-variant: small-caps;
}
.App {
  text-align: center;
}



